/*------------------------------------------------------------------
Color Picker
------------------------------------------------------------------*/

.Scp {
    width: 175px;
    height: 150px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: absolute;
    z-index: 1010;
    right: -0.5rem;
    top: 100%;
    margin-top: 0.7rem;
    border: 1px solid #dddddd;
    box-shadow: 0 13px 25px 0 rgba(0, 0, 0, 0.3);
}

.Scp-saturation {
    position: relative;
    width: calc(100% - 25px);
    height: 100%;
    background: -webkit-linear-gradient(left, #fff, #f00);
    background: linear-gradient(to right, #fff, #f00);
    float: left;
    margin-right: 5px;
}

.Scp-brightness {
    width: 100%;
    height: 100%;
    background: -webkit-linear-gradient(rgba(255, 255, 255, 0), #000);
    background: linear-gradient(rgba(255, 255, 255, 0), #000);
}

.Scp-sbSelector {
    border: 2px solid #fff;
    position: absolute;
    width: 14px;
    height: 14px;
    background: #fff;
    border-radius: 10px;
    top: -7px;
    left: -7px;
    box-sizing: border-box;
    z-index: 10;
}

.Scp-hue {
    width: 20px;
    height: 100%;
    position: relative;
    float: left;
    background: -webkit-linear-gradient(#f00 0%, #f0f 17%, #00f 34%, #0ff 50%, #0f0 67%, #ff0 84%, #f00 100%);
    background: linear-gradient(#f00 0%, #f0f 17%, #00f 34%, #0ff 50%, #0f0 67%, #ff0 84%, #f00 100%);
}

.Scp-hSelector {
    position: absolute;
    background: #fff;
    border-bottom: 1px solid #000;
    right: -3px;
    width: 10px;
    height: 2px;
}